// 怪物信息

const state = {
    current_bgm:null,
    bgm_volume:30,
    other_volume:30,
    bgm_melody_show:false,
}

const mutations = {
    SET_Current_Bgm:(state,current_bgm)=>{
        state.current_bgm = current_bgm
    },
    SET_Bgm_Volume:(state,bgm_volume)=>{
        state.bgm_volume = bgm_volume
    },
    SET_Other_Volume:(state,other_volume)=>{
        state.other_volume = other_volume
    },
    SET_BGM_MELODY_SHOW:(state,bgm_melody_show)=>{
        state.bgm_melody_show = bgm_melody_show
    }
}

const actions = {
    // 当前bgm信息
    Current_Bgm({commit},view){
        commit('SET_Current_Bgm', view)
    },
    // bgm音量
    Bgm_Volume({commit},view){
        commit('SET_Bgm_Volume', view)
    },
    // 其他音效音量
    Other_Volume({commit},view){
        commit('SET_Other_Volume', view)
    },
    // 是否展示bgm旋律
    Bgm_Melody_Show({commit},view){
        commit("SET_BGM_MELODY_SHOW",view)
    }
}

export default {
    state,
    mutations,
    actions
}