import _ from 'lodash'
import router from '@/router'
import common from './common';
import store from '@/store'
import * as TweenMax from 'gsap/all';

// 玩家对局所获取状态介绍
export function playerstatuinfo(scene,item){
    if(document.getElementById('playerstatuinfo')){
        return document.getElementById('playerstatuinfo').parentElement.removeChild(document.getElementById('playerstatuinfo'))
        // return false
    }
    var border = document.createElement("P");

    border.id = "playerstatuinfo";
    // border.style.width = "300px";
    // border.style.height = "200px";
    border.style.position = 'absolute';
    border.style.bottom = "calc(21% + 50px)"
    border.style.left = "25%"
    border.style.backgroundColor = "rgba(0,0,0,0.85)";
    border.style.borderImageSlice = "97 93 95 94";
    border.style.borderImageWidth = "20px 20px 20px 20px";
    border.style.borderImageOutset = "0px 0px 0px 0px";
    border.style.borderImageRepeat = "stretch stretch";
    border.style.borderImageSource = `url(${require("@/assets/image/border/border_2.svg")})`;
    border.style.borderStyle = "solid";
    border.innerHTML = `<p style="margin-left:10px;margin-right:10px;text-align:left"><span style="color:${item.titlecolor};font-family:Hywb">${item.name}</span>:</p>`;
    border.style.color = "white";
    border.style.fontFamily = "'幼圆', Courier, monospace";
    border.style.fontSize = "20px";
    border.style.maxWidth = "25%"
    // border.style.textAlign = "left"

    scene.appendChild(border)
}

// 主页面新游戏按钮
export function newgame(){
    if(!localStorage.getItem("archive")){
        // store.dispatch('Archive',this.archive)
        // router.push({name:"MainGame"})
        common.setArchive()
        router.push({name:"MainGame"})
    }else{
        var model = document.createElement('DIV');//幕布
        var back = document.createElement('DIV');//文字提示
        var close = document.createElement('DIV');//取消按钮
        var confirm = document.createElement('DIV');//确认按钮

        model.id = 'gameLoadDialog';
        model.style.width = '100%';
        model.style.height = '100%';
        model.style.position = 'absolute';
        model.style.zIndex = 12;
        model.style.top = 0;
        model.style.left = 0;
        model.style.backgroundColor = '#00000080';

        back.style.position = 'absolute';
        back.style.zIndex = 10;
        back.style.width = "20%";
        back.style.height = "10%";
        back.style.top = "40%";
        back.style.left = "40%";
        // back.style.border = "1px solid white",
        back.innerHTML = "是否覆盖当前存档？"
        back.style.textAlign = "center";
        back.style.fontSize = "30px";
        back.style.color = "white";
        back.style.letterSpacing = "10px";
        back.style.fontFamily = '"幼圆", Courier, monospace';

        close.style.position = 'absolute';
        close.style.zIndex = 10;
        close.style.width = "10%";
        close.style.height = "5%";
        close.style.top = "55%";
        close.style.left = "35%";
        // close.style.border = "1px solid white",
        close.innerHTML = "取消"
        close.style.textAlign = "center";
        close.style.fontSize = "30px";
        close.style.color = "white";
        close.style.letterSpacing = "10px";
        close.style.fontFamily = '"幼圆", Courier, monospace';
        close.style.cursor = 'pointer';
        close.style.transition = '0.1s all';
        close.onmouseenter = (e)=>{
            close.style.transform = "scale(1.08,1.08)"
        }
        close.onmouseleave = (e)=>{
            close.style.transform = "scale(1,1)"
        }
        close.onclick = (e)=>{
            model.parentElement.removeChild(model)
        }

        confirm.style.position = 'absolute';
        confirm.style.zIndex = 10;
        confirm.style.width = "10%";
        confirm.style.height = "5%";
        confirm.style.top = "55%";
        confirm.style.right = "35%";
        // confirm.style.border = "1px solid white",
        confirm.innerHTML = "确认"
        confirm.style.textAlign = "center";
        confirm.style.fontSize = "30px";
        confirm.style.color = "white";
        confirm.style.letterSpacing = "10px";
        confirm.style.fontFamily = '"幼圆", Courier, monospace';
        confirm.style.cursor = 'pointer';
        confirm.style.transition = '0.1s all';
        confirm.onmouseenter = (e)=>{
            confirm.style.transform = "scale(1.08,1.08)"
        }
        confirm.onmouseleave = (e)=>{
            confirm.style.transform = "scale(1,1)"
        }
        confirm.onclick = (e)=>{
            common.setArchive()
            model.parentElement.removeChild(model)
            router.push({name:"MainGame"})
        }

        document.body.appendChild(model);
        model.appendChild(back);
        model.appendChild(close);
        model.appendChild(confirm);
    }
}

//游戏加载界面
export function gameLoad(){
    // if(document.getElementById('gameLoadDialog')){
    //     return document.getElementById('modelDialog').parentElement.removeChild(document.getElementById('modelDialog'))
    // }
    // 幕布
    var model = document.createElement('DIV');
    // 正在加载的内容
    var main = document.createElement('DIV');

    model.id = 'gameLoadDialog'
    model.style.width = '100%';
    model.style.height = '100%';
    model.style.position = 'absolute';
    model.style.zIndex = 12;
    model.style.top = 0;
    model.style.left = 0;
    model.style.backgroundColor = '#00000080';

    main.style.position = 'absolute';
    main.style.zIndex = 10;
    main.style.top = '38%';
    main.style.left = '859px';
    main.style.width = '10%';
    main.style.height = '50px';
    main.style.border = '5px groove rgba(245, 245, 245, 0.5)';
    main.style.borderRadius = '10px';
    main.style.textAlign = 'center';
    main.style.lineHeight = '50px';
    main.style.letterSpacing = '5px';
    main.style.fontSize = '25px';
    main.style.color = 'honeydew';
    main.style.cursor = 'pointer';
    main.style.fontFamily =  '"幼圆", Courier, monospace';
    main.style.transition = '0.1s all';
    main.innerHTML = '主界面';
    
    document.body.appendChild(model);
    model.appendChild(main);
}

// esc菜单
export function escMenuEvent(){
    if(document.getElementById('modelDialog')){
        return document.getElementById('modelDialog').parentElement.removeChild(document.getElementById('modelDialog'))
    }
    // 幕布
    var model = document.createElement('DIV'),
    // 返回主界面
     main = document.createElement('DIV'),
    // 保存游戏
    option = document.createElement('DIV'),
    // 取消按键
    close = document.createElement('DIV');

    model.id = 'modelDialog'
    model.style.width = '100%';
    model.style.height = '100%';
    model.style.position = 'absolute';
    model.style.zIndex = 12;
    model.style.top = 0;
    model.style.left = 0;
    model.style.backgroundColor = '#00000080';

    main.style.position = 'absolute';
    main.style.zIndex = 10;
    main.style.top = '38%';
    main.style.left = '50%';
    main.style.transform = "translate(-50%,-50%)"
    main.style.textAlign = 'center';
    main.style.letterSpacing = '5px';
    main.style.fontSize = '25px';
    main.style.color = 'honeydew';
    main.style.cursor = 'pointer';
    main.style.transition = '0.1s all';
    main.innerHTML = "<i class='icon-tl' style='vertical-align:bottom'/><span style='font-family:幼圆,Courier,monospace'> 主界面 </span><i class='icon-tr' style='vertical-align:bottom'/>";
    main.onmouseenter = (e)=>{
        main.style.color = '#7ed8cc';
    };
    main.onmouseleave = (e)=>{
        main.style.color = 'honeydew';
    };
    main.onclick = (e)=>{
        router.push({name:'game'});
        model.parentElement.removeChild(model)
    }

    option.style.position = 'absolute';
    option.style.zIndex = 10;
    option.style.top = 'calc(38% + 100px)';
    option.style.left = '50%';
    option.style.textAlign = 'center';
    option.style.transform = "translate(-50%,-50%)"
    option.style.letterSpacing = '5px';
    option.style.fontSize = '25px';
    option.style.color = 'honeydew';
    option.style.cursor = 'pointer';
    option.style.transition = '0.1s all';
    option.innerHTML = "<i class='icon-tl' style='vertical-align:bottom'/><span style='font-family:幼圆,Courier,monospace'> 保存游戏 </span><i class='icon-tr' style='vertical-align:bottom'/>";
    option.onmouseenter = (e)=>{
        option.style.color = '#7ed8cc';
    };
    option.onmouseleave = (e)=>{
        option.style.color = 'honeydew';
    };
    option.onclick = (e)=>{
        common.updataArchive();
        model.parentElement.removeChild(model)
    }

    close.style.position = 'absolute';
    close.style.zIndex = 10;
    close.style.top = 'calc(38% + 200px)';
    close.style.left = '50%';
    close.style.textAlign = 'center';
    close.style.transform = "translate(-50%,-50%)"
    close.style.letterSpacing = '5px';
    close.style.fontSize = '25px';
    close.style.color = 'honeydew';
    close.style.cursor = 'pointer';
    close.style.transition = '0.1s all';
    close.innerHTML = "<i class='icon-tl' style='vertical-align:bottom'/><span style='font-family:幼圆,Courier,monospace'> 取消 </span><i class='icon-tr' style='vertical-align:bottom'/>";
    close.onmouseenter = (e)=>{
        close.style.color = '#7ed8cc';
    };
    close.onmouseleave = (e)=>{
        close.style.color = 'honeydew';
    };
    close.onclick = (e)=>{
        model.parentElement.removeChild(model)
    }
    
    document.body.appendChild(model);
    model.appendChild(main);
    model.appendChild(option);
    model.appendChild(close);

    if(router.history.current.name == 'CombatMain'){
        var exitGame = document.createElement('DIV');
        exitGame.style.position = 'absolute';
        exitGame.style.zIndex = 10;
        exitGame.style.top = 'calc(38% + 300px)';
        exitGame.style.left = '50%';
        exitGame.style.textAlign = 'center';
        exitGame.style.transform = "translate(-50%,-50%)"
        exitGame.style.lineHeight = '50px';
        exitGame.style.letterSpacing = '5px';
        exitGame.style.fontSize = '25px';
        exitGame.style.color = 'honeydew';
        exitGame.style.cursor = 'pointer';
        exitGame.style.fontFamily =  '"幼圆", Courier, monospace';
        exitGame.style.transition = '0.1s all';
        exitGame.innerHTML = "<i class='icon-tl' style='vertical-align:bottom'/><span style='font-family:幼圆,Courier,monospace'> 退出秘境 </span><i class='icon-tr' style='vertical-align:bottom'/>";
        exitGame.onmouseenter = (e)=>{
            exitGame.style.color = '#7ed8cc';
        };
        exitGame.onmouseleave = (e)=>{
            exitGame.style.color = 'honeydew';
        };
        exitGame.onclick = (e)=>{
            router.push({name:'CombatSelect'});
            model.parentElement.removeChild(model)
        }
        model.appendChild(exitGame);
    }
}

// 消息提示框
export function gameMessage(image,goods,num){
    var div = document.createElement("DIV");
    var id = "messageDiv" + document.getElementsByName("messageDiv").length;
    // var backImage = image;

    // div.name = "messageDiv";
    div.style.position = "absolute";
    div.style.zIndex = 10;
    div.name = "messageDiv"
    div.id = id;
    div.style.bottom = "30px";
    div.style.right = "30px";
    div.style.width = "20%";
    div.style.height = "100px";
    // div.style.border = "1px solid green";
    div.style.borderRadius = '8px';
    div.style.backgroundImage = `url(${image})`;
    div.style.backgroundSize = "cover";
    div.style.backgroundPosition = "50% 50%";
    div.style.backgroundRepeat = "no-repeat";
    div.style.fontSize = "20px";
    div.style.lineHeight = "100px";
    div.style.textAlign = "right";
    div.style.color = "black";
    div.style.fontFamily =  '"幼圆", Courier, monospace';
    // div.style.textShadow = "#FC0 1px 0 10px"
    div.innerText = `获得物品：${goods} × ${num}!`;
    div.style.boxShadow = "0px 0px 10px 5px #FFFFFF";
    div.style.opacity = 0;
    // div.style.clipPath = "inset(0 0 0 100%)"

    document.body.appendChild(div);

    TweenMax.gsap.to(`#${id}`,0.5,{opacity:1,ease:TweenMax.Power0.easeNone,onComplete:()=>{
        TweenMax.gsap.to(`#${id}`,0.5,{opacity:0,delay:4,onComplete:()=>{
            document.getElementById(id).parentElement.removeChild(document.getElementById(id))
        }})
    }})
}

// 创建卡牌模板
export function create_card_template(obj){
    var svg = document.createElementNS("http://www.w3.org/2000/svg","svg");

    svg.id = obj.id;
    svg.style.position = "absolute";
    svg.style.zIndex = 9;
    svg.style.width = "120px";
    svg.style.height = "180px";
    svg.style.border = "1px solid rgba(0, 0, 0, 0.3)";
    svg.style.top = "10px";
    svg.style.left = "0px";
    svg.style.transition = "0.1s";
    svg.style.borderRadius = "5px";
    svg.style.cursor = "pointer";
    svg.style.boxShadow = "";
    svg.style.backgroundImage = `url(${require('@/assets/svg/4-ai.svg')})`;
    svg.style.backgroundSize = "cover";
    svg.style.backgroundPosition = "50% 50%";
    svg.style.backgroundRepeat = "no-repeat";
    svg.onmouseenter = (e)=>{
        svg.style.zIndex = 10;
        svg.style.top = "0px";
    };
    svg.onmouseleave = (e)=>{
        svg.style.zIndex = 9;
        svg.style.top = "10px";
    };
    svg.onclick = (e)=>{
        if(store.state.player.start_end == true){
            return false
        }
        var cardid = store.state.player.cardid
        if(_.isEmpty(cardid) == true){
            store.dispatch("Cardid",obj)
            document.getElementById(obj.id).style.boxShadow = `0px 0px 5px 5px ${obj.FColor}`
        }else if(_.isEmpty(cardid) == false && cardid.id !== obj.id){
            document.getElementById(cardid.id).style.boxShadow = ''
            store.dispatch("Cardid",obj)
            document.getElementById(obj.id).style.boxShadow = `0px 0px 5px 5px ${obj.FColor}`
        }else if(cardid.id == obj.id){
            document.getElementById(cardid.id).style.boxShadow = ''
            store.dispatch("Cardid",{})
        }
    }

    var defs = document.createElementNS("http://www.w3.org/2000/svg","defs"),
        path = document.createElementNS("http://www.w3.org/2000/svg","path"),
        foreignObject = document.createElementNS("http://www.w3.org/2000/svg","foreignObject"),
        p = document.createElementNS("http://www.w3.org/1999/xhtml","p"),
        line = document.createElementNS("http://www.w3.org/2000/svg","line"),
        text = document.createElementNS("http://www.w3.org/2000/svg","text"),
        textPath = document.createElementNS("http://www.w3.org/2000/svg","textPath"),
        width = 120,
        height = 180;

    path.setAttribute("id","pt1")
    path.setAttribute("d",`M${width*0.06} ${height*0.25} L${width*0.94} ${height*0.25}`)
    path.setAttribute("fill","none")
    defs.appendChild(path)

    foreignObject.setAttribute("x","0")
    foreignObject.setAttribute("y","0")
    foreignObject.setAttribute("width","100%")
    foreignObject.setAttribute("height","100%")
    foreignObject.style.backgroundColor = "#ffffffbe"
    foreignObject.style.fontFamily = "幼圆,Courier,monospace"

    p.style.width = "80%"
    p.style.height = "80%"
    p.style.marginTop = "50%"
    p.style.marginLeft = "10%"
    p.innerHTML = obj.FTextDescription
    foreignObject.appendChild(p)

    line.setAttribute("x1","5%")
    line.setAttribute("y1","23%")
    line.setAttribute("x2","5%")
    line.setAttribute("y2","90%")
    line.setAttribute("stroke",obj.FColor)
    line.setAttribute("stroke-width","1")
    

    textPath.setAttribute("text-anchor","middle")
    textPath.setAttribute("startOffset","50%")
    textPath.setAttribute("fill","black")
    textPath.setAttribute("href","#pt1")
    textPath.style.fontSize = "20px"
    textPath.style.fontFamily = "Hywb"
    textPath.innerHTML = obj.FTitle
    text.appendChild(textPath)

    svg.appendChild(defs)

    svg.appendChild(foreignObject)

    svg.appendChild(line)

    svg.appendChild(text)

    // var width = 120,
    //     height = 180;
    //     html = `
    //     <defs>
    //         <path id="pt2" d="M${width*0.06} ${height*0.25} L${width*0.94} ${height*0.25}" fill="none"></path>
    //     </defs>
    //     <foreignObject x="0" y="0" width="100%" height="100%" style="background-color:#ffffffbe;font-family:幼圆,Courier,monospace;">
    //         <p style="width:80%;height:70%;margin-top:50%;margin-left:10%;">${obj.FTextDescription}</p>
    //     </foreignObject>
    //     <line x1="5%" y1="23%" x2="5%" y2="90%" stroke="${obj.FColor}" stroke-width="1"></line>
    //     <text>
    //         <textPath style="font-size:20px;font-family:Hywb;" text-anchor="middle" startOffset="50%" fill="black" xlink:href="#pt2">${obj.FTitle}</textPath>
    //     </text>`;

    // svg.insertAdjacentHTML("afterbegin",html)

    return svg
}
export default {
    playerstatuinfo,
    newgame,
    escMenuEvent,
    gameLoad,
    gameMessage,
    create_card_template
}