import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// import * as TweenMax from 'gsap/all'

// import { encryptLong, decryptLong } from '@/utils/jsencrypt';
import common from '@/api/common'

import '@/assets/font/svg_font.scss'

//axios
import axios from 'axios'
Vue.prototype.$axios = axios
// axios.defaults.baseURL='/';
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

Vue.config.productionTip = false

router.beforeEach((to,from,next)=>{
  if(to.name == "game" && from.name !== null && from.name !== "TransitView"){
    common.updataArchive(store.state.archive.archive)
    next()
    // common.Switch_Bgm(to.name)
  }else{
    next()
    if(from.name !== null){
      // common.Switch_Bgm(to.name)
    }
  }
})

new Vue({
  axios,
  router,
  store,
  render: h => h(App)
}).$mount('#app')
