// 玩家信息

const state = {
    cardid:{},
    start_end:false,
    player_status:{},
    player_attrib:{}
}

const mutations = {
    SET_Cardid:(state,cardid)=>{
        state.cardid = cardid
    },
    SET_Start_End:(state,start_end)=>{
        state.start_end = start_end
    },
    SET_Player_Status:(state,player_status)=>{
        state.player_status = player_status
    },
    SET_Player_ATTRIB:(state,player_attrib)=>{
        state.player_attrib = player_attrib
    },
}

const actions = {
    // 玩家当前所选中卡牌
    Cardid({commit},view){
        commit('SET_Cardid', view)
    },
    // 是否处于回合对战中
    Start_End({commit},view){
        commit('SET_Start_End', view)
    },
    // 玩家状态栏
    Player_Status({commit},view){
        commit('SET_Player_Status', view)
    },
    // 玩家各项属性
    Player_ATTRIB({commit},view){
        commit('SET_Player_ATTRIB', view)
    },
}

export default {
    state,
    mutations,
    actions
}