// 怪物信息

const state = {
    monster_info:{},
    monster_status:[],
    monster_attrib:{}
}

const mutations = {
    SET_Monster_Info:(state,monster_info)=>{
        state.monster_info = monster_info
    },
    SET_Monster_Status:(state,monster_status)=>{
        state.monster_status = monster_status
    },
    SET_Monster_ATTRIB:(state,monster_attrib)=>{
        state.monster_attrib = monster_attrib
    },
}

const actions = {
    // 当前怪物信息
    Monster_Info({commit},view){
        commit('SET_Monster_Info', view)
    },
    // 怪物状态栏
    Monster_Status({commit},view){
        commit('SET_Monster_Status', view)
    },
    // 怪物各项属性
    Monster_ATTRIB({commit},view){
        commit('SET_Monster_ATTRIB', view)
    },
}

export default {
    state,
    mutations,
    actions
}