<template>
  <div id="app">
    <!-- <nav>
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </nav> -->
    <router-view />
    <div class="left_menu" @mouseenter="left_menu_mouseenter"></div>
    <div class="left_menu_model" id="left_menu_model" v-if="left_menu_show" @mouseenter="left_menu_model_mouseenter" @mouseleave="left_menu_model_mouseleave">
      <div class="text_volume_left_bottom" id="text_volume_left_bottom" :title="bgm_title" @click="bgm_title_click">{{music_consig.bgm_volume}}</div>
      <div class="text_volume_right_bottom" id="text_volume_right_bottom" :title="other_title" @click="other_title_click">{{music_consig.other_volume}}</div>
      <input class="bgm_volume" id="bgm_volume" type="range" min="0" max="100" :value="music_consig.bgm_volume" @input="bgmVolumeChange"/>
      <input class="other_volume" id="other_volume" type="range" min="0" max="100" :value="music_consig.other_volume" @input="otherVolumeChange"/>
    </div>
    <canvas v-if="$store.state.bgm.bgm_melody_show" id="bgm_canvas" class="canvas"/>
  </div>
</template>

<script>
import components from "@/api/components.js"
import common from "@/api/common.js"
import * as TweenMax from 'gsap/all'
import _ from 'lodash'

export default {
  data() {
    return {
      left_menu_show:false,//菜单开关
      setTimeOutID:[],//延时序列
      music_consig:{
        bgm_volume:0,
        other_volume:0
      },
      bgm_title:"关闭音乐",
      other_title:"关闭音效"
    };
  },
  watch:{
    // "$store.state.bgm.current_bgm"(nval,oval){
    //   console.log(nval.src);
    // },
    left_menu_show(nval,oval){
      if(nval == true){
        // if(JSON.parse(localStorage.BGM_SWITCH).statue == false){
        //   this.$nextTick(()=>{
        //     this.bgm_title = "打开音乐"
        //     document.getElementById("bgm_volume").setAttribute("disabled",true)
        //     document.getElementById("text_volume_left_bottom").style.background = "linear-gradient(45deg,transparent 49.5%, rgb(0,0,0) 49.5%, #ddd 50.5%, transparent 50.5%),rgba(255, 255, 255, 0.7)"
        //   })
        // }else{
        //   this.bgm_title = "关闭音乐"
        // }
        // if(JSON.parse(localStorage.OTHER_SWITCH).statue == false){
        //   this.other_title = "打开音效"
        //   this.$nextTick(()=>{
        //     document.getElementById("other_volume").setAttribute("disabled",true)
        //     document.getElementById("text_volume_right_bottom").style.background = "linear-gradient(45deg,transparent 49.5%, rgb(0,0,0) 49.5%, #ddd 50.5%, transparent 50.5%),rgba(255, 255, 255, 0.7)"
        //   })
        // }else{
        //   this.other_title = "关闭音效"
        // }
      }
    },
    "$store.state.bgm.bgm_volume"(nval,oval){
      this.music_consig.bgm_volume = nval;
      this.$store.state.bgm.current_bgm.volume = +nval / 100;
    },
    "$store.state.bgm.other_volume"(nval,oval){
      this.music_consig.other_volume = nval;
    },
    "$store.state.bgm.bgm_melody_show"(nval,oval){
      if(nval == true){
        this.$nextTick(()=>{
          common.draw_init()
        })
      }
    }
  },
  mounted() {
    window.addEventListener("beforeunload", (e) => {
      if (this.$router.history.current.name !== "game" || this.$router.history.current.name !== "TransitView") {
        e.returnValue = "";
      }
    });
    window.addEventListener("keyup", (e) => {
      if (this.$router.history.current.name !== "game" && e.code == "Backquote") {
        components.escMenuEvent();
      }
    });
    document.getElementById('app').oncontextmenu = (e)=>{
      e.preventDefault()
    }
    this.$store.dispatch("Current_Bgm", new Audio())
    // if(!localStorage.BGM_SWITCH){
    //   localStorage.BGM_SWITCH = JSON.stringify({statue:true,volume:this.$store.state.bgm.bgm_volume})
    // }
    // if(!localStorage.OTHER_SWITCH){
    //   localStorage.OTHER_SWITCH = JSON.stringify({statue:true,volume:this.$store.state.bgm.other_volume})
    // }
  },
  methods: {
    // 移入左侧唤出菜单区域事件
    left_menu_mouseenter(e){
      if(this.left_menu_show == false){
        const bgm_volume = this.$store.state.bgm.bgm_volume,
        other_volume = this.$store.state.bgm.other_volume;
        this.music_consig.bgm_volume = bgm_volume
        this.music_consig.other_volume = other_volume
        this.left_menu_show = true
        this.$nextTick(()=>{
          document.getElementById("bgm_volume").style.background = `linear-gradient(to right, #e66465 0%, #9198e5b4 ${bgm_volume}%, #ffffff00 100%)`
          document.getElementById("other_volume").style.background = `linear-gradient(to right, #e66465 0%, #9198e5b4 ${other_volume}%, #ffffff00 100%)`
          TweenMax.gsap.to("#left_menu_model",0.3,{left:10,opacity:1,ease:TweenMax.Power0.easeNone,onComplete:()=>{
            var setTimeOutID = setTimeout(() => {
              TweenMax.gsap.to("#left_menu_model",0.3,{left:-102,opacity:0,ease:TweenMax.Power0.easeNone,onComplete:()=>{
                this.left_menu_show = false
              }})
              this.setTimeOutID = []
            }, 3000);
            this.setTimeOutID.push(setTimeOutID)
          }})
        })
      }
    },
    // 移入左侧菜单事件
    left_menu_model_mouseenter(e){
      for (let i = 0; i < this.setTimeOutID.length; i++) {
        clearTimeout(this.setTimeOutID[i]);
      }
    },
    // 移出左侧菜单事件
    left_menu_model_mouseleave(e){
      var setTimeOutID = setTimeout(() => {
        TweenMax.gsap.to("#left_menu_model",0.3,{left:-102,opacity:0,ease:TweenMax.Power0.easeNone,onComplete:()=>{
          this.left_menu_show = false
        }})
        this.setTimeOutID = []
      }, 3000);
      this.setTimeOutID.push(setTimeOutID)
    },
    bgmVolumeChange(e){
      this.$store.dispatch("Bgm_Volume",e.target.value)
      e.target.style.background = `linear-gradient(to right, #e66465 0%, #9198e5b4 ${e.target.value}%, #ffffff00 100%)`
    },
    otherVolumeChange(e){
      this.$store.dispatch("Other_Volume",e.target.value)
      e.target.style.background = `linear-gradient(to right, #e66465 0%, #9198e5b4 ${e.target.value}%, #ffffff00 100%)`
    },
    bgm_title_click(e){
      if(this.bgm_title == "关闭音乐"){
        this.$store.state.bgm.current_bgm.pause()
        this.bgm_title = "打开音乐"
        document.getElementById("bgm_volume").setAttribute("disabled",true)
        e.target.style.background = "linear-gradient(45deg,transparent 49.5%, rgb(0,0,0) 49.5%, #ddd 50.5%, transparent 50.5%),rgba(255, 255, 255, 0.7)"
      }else if(this.bgm_title == "打开音乐"){
        this.bgm_title = "关闭音乐"
        common.Switch_Bgm(this.$router.history.current.name)
        document.getElementById("bgm_volume").setAttribute("disabled",false)
        e.target.style.background = "rgba(255, 255, 255, 0.7)"
      }
    },
    other_title_click(e){
      if(this.other_title == "关闭音效"){
        this.other_title = "打开音效"
        document.getElementById("other_volume").setAttribute("disabled",true)
        e.target.style.background = "linear-gradient(45deg,transparent 49.5%, rgb(0,0,0) 49.5%, #ddd 50.5%, transparent 50.5%),rgba(255, 255, 255, 0.7)"
      }else if(this.other_title == "打开音效"){
        this.other_title = "关闭音效"
        document.getElementById("other_volume").setAttribute("disabled",false)
        e.target.style.background = "rgba(255, 255, 255, 0.7)"
      }
    },
  },
};
</script>

<style lang="scss">

body {
  height: 100%;
  margin: 0px;
  user-select: none;
}

html {
  height: 100%;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}

nav {
  padding: 30px;

  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
.load_style{
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #000000;
  transition: all 1.5s;
}
.left_menu{
  position: absolute;
  z-index: 11;
  width: 2px;
  height: 100%;
  top: 0;
}
.left_menu_model{
  position: absolute;
  z-index: 11;
  width: 115px;
  height: 372px;
  // border: 1px solid white;
  top: 10%;
  left: -102px;
  opacity: 0;
  // transition: all 1s;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  font-family: "幼圆", Courier, monospace;
}
.bgm_volume{
  width: 300px;
  left: calc(0% - 120px);
  border: .1px solid rgba(245, 245, 245, 0.7);
}
.other_volume{
  width: 300px;
  right: calc(0% - 120px);
  border: .1px solid rgba(245, 245, 245, 0.7);
}
.text_volume_left_bottom{
  position: absolute;
  bottom: 0;
  left: 18px;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 10px;
  cursor: pointer;
}
.text_volume_right_bottom{
  position: absolute;
  bottom: 0;
  right: 18px;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 10px;
  cursor: pointer;
}
input[type=range]{
  -webkit-appearance: none;
  position: absolute;
  bottom: calc(0% + 165px);
  transform: rotate(-90deg);
  border-radius: 10px;
  background: linear-gradient(to right, #e66464 0%, #9198e5b4 0%, #ffffff00 100%);
}
input[type=range]::-webkit-slider-runnable-track {
  height: 30px;
  border-radius: 10px;
  /*将轨道设为圆角的*/
  // background-color: #ffffff7c;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 30px;
  width: 30px;
  border-radius: 10px;
  background-color: #91d3d3c5;
  &:hover{
    background-color: #91d3d3;
    box-shadow: 0px 0px 13px 4px #ffffff;
  }
}
.canvas{
  position: absolute;
  z-index: 11;
  left: 0;
  bottom: 0;
  // border: 1px solid white;
}
</style>
