// 时间戳格式化
export function parseTime(date, pattern) {
    var fmt = pattern || 'YYYY-mm-dd HH:MM'
    let ret = "";
    date = new Date(date);
    const opt = {
        'Y+': date.getFullYear().toString(), // 年
        'm+': (date.getMonth() + 1).toString(), // 月
        'd+': date.getDate().toString(), // 日
        'H+': date.getHours().toString(), // 时
        'M+': date.getMinutes().toString(), // 分
        'S+': date.getSeconds().toString() // 秒
    }
    for (let k in opt) {
        ret = new RegExp('(' + k + ')').exec(fmt)
        if (ret) {
            fmt = fmt.replace(
                ret[1],
                ret[1].length == 1 ? opt[k] : opt[k].padStart(ret[1].length, '0')
            )
        }
    }
    return fmt
}

// 玩家初始手牌组生成
export async function create_card(flag,data){
    var list = [],
    dicelist = _.sampleSize([1,2,3],2)
    
    if(flag == 0){ // 0:开局发牌
        dicelist.forEach((item,index)=>{
            switch (item) {
                case 1:
                    list = Get_Basics_Card(7)
                    break;
                default:
                    break;
            }
        })
    }
    if(flag == 1){ // 1:中场发牌
        dicelist.forEach((item,index)=>{
            switch (item) {
                case 1:
                    list = Get_Basics_Card(4)
                    break;
                default:
                    break;
            }
        })
    }
    return list
}

export async function Get_Basics_Card(e){
    var basicslist = [],
        list = [];
    await axios.get("/json/Basics_Card.json").then((res)=>{
        basicslist = _.sampleSize(res.data,e)
        basicslist.forEach((item,index)=>{
            item.id = _.uniqueId('card')
            item.FId = index + 1
            item.FVerticalDrawing = require('@/assets/playercard/' + item.FVerticalDrawing + '.svg')
            item.FTopDecoration = require('@/assets/image/decorate/' + item.FTopDecoration + '.svg')
            list.push(components.create_card_template(item))
        })
    })
    return list
}

// 切换BGM
export function Switch_Bgm(name){
    var config = {
        src:null,
        volume:0
    }
    switch (name) {
        case "game":
            config.src = require("@/assets/audio/Where Is the Love.mp3")
            config.volume = store.state.bgm.bgm_volume
            Play_Bgm(config)
            break;
    
        default:
            break;
    }
}
// 播放BGM
export function Play_Bgm(config){
    store.state.bgm.current_bgm.src = config.src
    store.state.bgm.current_bgm.volume = config.volume / 100
    store.state.bgm.current_bgm.play()
    store.state.bgm.bgm_melody_show = true
}
// bgm旋律可视化初始化
export function draw_init(){
    // 当前bgm旋律可视化
    var canvas = document.getElementById("bgm_canvas");
    var WIDTH = canvas.width,
    HEIGHT = canvas.height,
    canvasCtx = canvas.getContext("2d");
    var context = new (window.AudioContext || window.webkitAudioContext)()
    var analyser = context.createAnalyser()
    const source = context.createMediaElementSource(store.state.bgm.current_bgm);
    source.connect(analyser);
    source.connect(context.destination)
    analyser.fftSize = 512;
    canvasCtx.clearRect(0, 0, WIDTH, HEIGHT);
    let grd = canvasCtx.createLinearGradient(0,150,0,0)
    // grd.addColorStop(0,"rgb(148,177,255)")
    // grd.addColorStop(1,"rgba(148,177,255,0)")
    grd.addColorStop(0,"#10ab80")
    grd.addColorStop(1,"#10ab8000")
    draw(analyser,WIDTH,HEIGHT,canvasCtx,grd)
}
// 将bgm旋律可视化效果展示
export function draw(analyser,WIDTH,HEIGHT,canvasCtx,grd) {
    var _bufferLength = analyser.frequencyBinCount,
    _dataArray = new Uint8Array(_bufferLength),
    _grd = grd;
    sessionStorage.REQUEST_ANIMATION_FRAME = requestAnimationFrame(()=>draw(analyser,WIDTH,HEIGHT,canvasCtx,_grd));
    analyser.getByteFrequencyData(_dataArray);
    canvasCtx.clearRect(0, 0, WIDTH, HEIGHT);
    const barWidth = (WIDTH / _bufferLength) * 2.5;
    let barHeight;
    let x = 0;
    for(var i = 0; i < _bufferLength; i++) {
        barHeight = _dataArray[i];
        // canvasCtx.fillStyle = "rgb(" + (barHeight + 100) + ",50,50)";
        canvasCtx.fillStyle = _grd;
        canvasCtx.fillRect(
            x,
            HEIGHT - barHeight / 2,
            barWidth,
            barHeight / 2
        );
        x += barWidth * 1.3;
    }
}

// 长时间不操作，展示bgm旋律可视化
export function show_bgm_melody(){
    sessionStorage.HANDLE_WATCH = setTimeout(() => {
        if(store.state.bgm.current_bgm.src !== null){
            store.state.bgm.bgm_melody_show = true
        }
    }, 300000);
}

export default {
    create_card,
    setArchive,
    updataArchive,
    parseTime,
    Switch_Bgm,
    Play_Bgm,
    draw_init,
}